#goodItem{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 3vh;
}
#goodItem.line{
    box-sizing: border-box;
    padding-top: 7vh;
    padding-bottom: 3.5vh;
    
    border-top: .2vh solid black;
}
#goodItem .sec-1{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
}
#goodItem .sec-1>.description{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 1.2vh;
}

#goodItem .sec-1>.description h1{
    font-family: 'Mak';
}

#goodItem .sec-1>.description p{
    font-family: 'Nitti';
}
#goodItem .sec-1> img{
    height: 12vh;
    border-radius: 3vw;
}

#goodItem .sec-2{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    gap: 1vh;
}
#goodItem .sec-2 .delete{
    background-color: transparent;

    text-decoration: underline;
    font-family: 'Nitti';

    border: none;
}
#goodItem .sec-2>.price{
    box-sizing: border-box;
    padding: 1vh;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    gap: .5vh;

    width: fit-content;

    border: .1vh solid black;
}
#goodItem .sec-2>.price p{
    font-family: 'Mak';
}

#goodItem .sec-3 div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    border-top: .2vh solid black;
    border-bottom: .2vh solid black;

    height: 7vh;

    overflow: hidden;
}
#goodItem .sec-3 div button{

    background-color: transparent;
    border: none;
    border-left: .2vh solid black;
    border-right: .2vh solid black;

    width: 7vh;
    height: 100%;
}

#goodItem .sec-3>button{
    align-self: center;

    font-family: 'Nitti';
    color: white;

    height: 7vh;
    width: 100%;
    color: black !important;

    background-color: black;
    border: none;
}
#goodItem .sec-3>button.remove{
    align-self: center;

    font-family: 'Nitti';
    color: black !important;

    height: 7vh;
    width: 100%;

    background-color: transparent;
    border: .2vh solid black;
}

