.newItem{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 2vh;

    width: 100%;
}
.newItem p{
    font-family: 'Nitti';
    font-size: 2.3vh;
}
.newItem a{
    font-family: 'Nitti';
    font-size: 2.3vh;
    color: black;
}
.newItem h1{
    font-family: 'Mak';
    font-size: 4vh;
}
.newItem image{
    width: 100%;
}
.newItem .buttonLink{
    align-self: flex-start;

    font-family: 'Nitti';
    font-size: 2.3vh;
    text-decoration: underline;

    background-color: transparent;
    border: none;
}