#contackWindow{
    box-sizing: border-box;
    padding: 7vw;
    padding-top: 0vw;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 3vh;
}
#contackWindow section{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 1vw;
}
#contackWindow h1{
    font-family: 'Mak';
}
#contackWindow p{
    font-family: 'Nitti';
}
#contackWindow a{
    font-family: 'Nitti';
    color: black;
}

#contackWindow form{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 2vh;
}
#contackWindow form input::placeholder{
    font-family: 'Nitti';
}
#contackWindow form input{
    box-sizing: border-box;
    padding-left: 2vh;

    font-family: 'Nitti';

    height: 7vh;
    border: .2vh solid black;
}
#contackWindow form input.true{
    background-color: rgba(62, 142, 62, 0.5);
    color: white;
}
#contackWindow form input.false{
    background-color: rgba(142, 62, 62, 0.5);
    color: white;
}
#contackWindow form button{
    align-self: center;

    font-family: 'Nitti';
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}
