#loading{
    position: absolute;
    top:0vh;
    left:0vh;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: stretch;

    height: 100%;
    width: 100%;

    background-color: white;

    z-index: 99;
}
#loading>section{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    height: 70%;

}
#loading>section>img{
    height: 40vh;
    ;
}

#loading>section .loading-el{
    position: relative;

    width: 100%;
    height: 5vh;
}

#loading>section .loading-el>p{
    position: absolute;
    left:50%;
    top:0vh;

    font-family: 'Mak';
    
    transition: .5s;
}
#loading>section .loading-el>.loading-bar-bg{
    position: absolute;
    left:0vh;
    top: 50%;

    width: 100%;
    height: 25%;

    content: none;
    border-bottom: .2vh solid black;

}
#loading>section .loading-el>.loading-line{
    position: absolute;
    left:0vh;
    top: 50%;

    height: 25%;
    content: none;
    background-color: black;

    transition: .5s;
}