#menuLists{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

    margin-bottom: 7vw;
}

#menuLists .menuList{
    box-sizing: border-box;
    padding-left: 5vh;
    padding-right: 5vh;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 2vh;
    overflow-x: auto;

    height: 8vh;

    /* border-top: .1vh solid black; */
    border-bottom: .2vh solid black;
}

#menuLists .menuList::-webkit-scrollbar{
    display: none;
}

#menuLists .menuList button{
    box-sizing: border-box;
    padding: 1vh;

    font-family: 'Mak';
    color: black;
    font-size: 4vh;

    /* height: 4vh; */
    /* min-width: 40vh; */
    width: fit-content;
    /* background-color: red; */
    
    border: none;
    background-color: transparent;
    
}
#menuLists .menuList button.select{
    -webkit-text-stroke: .1vh black;
    color: white;
}

#menuWindow{
    box-sizing: border-box;
    padding: 7vw;
    padding-top: 0vw;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

    gap: 3vh;

    min-height: 60vh;
}
#menuWindow>p{
    font-family: 'Mak';
    text-align: center;
}
#menuWindow::-webkit-scrollbar{
    display: none;
}