.orderItem{
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3vh;

    box-sizing: border-box;
    padding: 1vh;

    height: 30vh;
    min-width: 100%;

    border: .2vh solid black;
    border-radius: 5vh;
}
.orderItem>.orderId{
    position: absolute;
    top: 3vh;
    left: 3vh;
}

.orderItem .orderStatus{
    font-family: 'Mak';
    text-align: center;
}

.orderItem>button{
    align-self: center;

    font-family: 'Nitti';
    
    color: white;

    height: 6vh;
    width: 90%;

    background-color: black;
    border: none;
}