#newsWindow{
    box-sizing: border-box;
    padding: 7vw;
    padding-top: 0vw;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 3vh;
}

#newsWindow.center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
}
#newsWindow .newsNull{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    gap: 2vh;
}
#newsWindow .newsNull p{

    font-family: 'Mak';
    text-align: center;
    font-size: 4vh;
}
#newsWindow .newsNull button{
    align-self: center;

    font-family: 'Nitti';
    font-size: 2.5vh;
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}