.galary{
    position: relative;
    
    max-width: 100%;
    height: 45vh;

    overflow-x: hidden;
    overflow-y: hidden;
}
.glaryBox{
    position: absolute;
    top:0px;
    left: calc(50% - ((1*(100%/3.5)) + 1vw));
    
    width: fit-content;
    height: 100%;

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1vw;

    transition: .3s;
}
.glaryBox .galaryItem{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2vh;

    height: fit-content;
    width: 40vh;

    transition: .3s;

    opacity: .5;

    overflow: hidden;

    transform: scale(.8);

}
.glaryBox .galaryItem>p{
    font-family: 'Mak';
    font-size: 2.5vh;
    align-self: flex-start;
}

.glaryBox .galaryItem>img{
    width: 100%;
    height: 25vh;
    border-radius: 3vh;
}

.glaryBox .bigItem{
    transform: scale(1.1);
    opacity: 1;
    /* box-shadow: 0px .5vh .5vh rgba(0, 0, 0, 0.5); */

}

.galaryArrowBck,
.galaryArrowFwd{
    position: absolute;

    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    font-size: 3vw;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.505);
    
    transition: .3s;

    background-color: transparent;
    border: none;
    z-index: 1;
}
.galaryArrowBck:hover,
.galaryArrowFwd:hover{
    transform: scale(1.1);
    color: rgb(255, 255, 255);
}

.galaryArrowBck{
    top: 50%;
    left: 5%;
}

.galaryArrowFwd{
    top: 50%;
    left: 93%;
}

.doters{
    position: absolute;
    top: 90%;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1vh;

    width: 100%;
    height: 5vh;
}
.doters span{
    display: block;
    border: .1vh solid black;
    border-radius: 50vh;

    height: 1vh;
    width: 1vh;
}
.doters span.fill{
    background-color: black;
}
