#headerMenu{
    position: absolute;
    top: 0px;
    left: 0px;

    box-sizing: border-box;
    padding: 5vw;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    height: 10vh;
    width: 100%;
    border-bottom:  .1vw solid black;
}
#headerMenu>.menuSection{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 5vw;

    border: none;
    background-color: transparent;

}
#headerMenu>.menuSection button{
    position: relative;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    height: 6vh;
    width: 6vh;

    font-family: 'Mak';
    text-align: left;
    text-transform: uppercase;
    line-height: 6vw;
    color: black;

    /* border: 1px solid red; */
    border: none;
    background-color: transparent;
}

#headerMenu>.menuSection button .notify{
    position: absolute;
    top: 0px;
    left: 70%;

    padding-left: .5vh;
    padding-right: .5vh;

    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: 5vw;

    border-radius: 50vw;
    background-color: black;

    font-family: 'Nitti';
    font-size: 4vw;
    color: white;

}

.menuSection-backToSite>button{
    height: 80%;
    width: 35vw;

    border: none;
    background-color: black;
    color: white;

    font-family: 'Nitti';
    font-size: 1.5vh;
}
.menuSection-backToSite{
    height: 100%;
}