#qrCodeWindow{
    box-sizing: border-box;
    padding: 7vw;
    padding-top: 0vw;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 3vh;
}

#qrCodeWindow form{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 2vh;
}
#qrCodeWindow form input::placeholder{
    font-family: 'Nitti';
}
#qrCodeWindow form input{
    box-sizing: border-box;
    padding-left: 2vh;

    font-family: 'Nitti';

    height: 7vh;
    border: .2vh solid black;
}
#qrCodeWindow form button{
    align-self: center;

    font-family: 'Nitti';
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}