#mainWindow{
    margin-top: 10vh;
    
    max-height: 90vh;

    overflow-y: auto;
}

#mainWindow .mainSection{
    box-sizing: border-box;
    padding-top: 5vh;
    padding-bottom: 8vh;

    /* height: 90vh; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

    overflow-y: auto;
}

#mainWindow .mainSection button{
    box-sizing: border-box;
    padding-left: 1vh;
    padding-right: 1vh;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin-left: 2vh;
    margin-right: 2vh;

    height: 10vh;

    background-color: transparent;
    border: none;
    border-top: .1vh solid black;
    border-bottom: .1vh solid black;

    transition: .5s;
}

#mainWindow .mainSection button.hide{
    transform: translateX(-10vh);
    opacity: 0;
}
#mainWindow .mainSection button.show{
    transform: translateX(0vh);
    opacity: 1;
}

#mainWindow .mainSection button p{
    font-family: 'Mak';
    color: black;
}