#navigationMenu{
    position: absolute;
    top: 0px;
    left: 0px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    gap: 2vh;

    background-color: black;


    height: 100vh;
    width: 100%;
    
    z-index: 1;
}
#navigationMenu>button{

    font-family: 'Mak';
    font-size: 4.5vh;
    color: white;

    height: 5vh;

    background-color: transparent;
    border: none;
    /* border: 1px solid red; */
}
#navigationMenu .closeButton{
    position: absolute;
    top: 85vh;
    left: 0px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    width: 100%;
}
#navigationMenu .closeButton>button{
    display: flex;
    justify-content: center;
    align-items: center;

    border: none;
    border-radius: 50vw;

    height: 9vh;
    width: 9vh;

    background-color: white;
}