p,a,h1,h2,h3,h4,h5,h6,div,section,article,span{
  color: black;
}
html{
  overflow-x: hidden ;
}
body{
  max-width: 100vw;
  overflow-x: hidden;
}

.App{
  position: relative;

  height: 100vh;
  width: 100vw;
  max-width: 100vw;

  overflow-y: auto;
  overflow-x: hidden;

}


/* ANIMATION */

[elstatus]{
  transition: .3s;
}

[elstatus='hide']{
  opacity: 0;
}
[elstatus='show']{
  transform: translateY(0px) ;
  opacity: 1;
}
[showfrom='center']{
  transform:translateY(0px) ;
}
[showfrom='left']{
  transform:translateX(50px) ;
}
[showfrom='right']{
  transform:translateX(-50px) ;
}
[showfrom='top']{
  transform:translateY(-50px) ;
}
[showfrom='bottom']{
  transform:translateY(50px) ;
}

/* FONTS SIZE */
.f-0::placeholder{
  font-size: 4.5vw;
}
.f-0{
  font-size: 4.5vw;
}
.f-1{
  font-size: 6vw;
}
.f-2{
  font-size: 8vw;
}

/* WINDOW */
.window{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;

  margin-top: 10vh;

  min-height: 90vh;
  max-height: 90vh;

  gap: 0vh;
}

.window-flex-column{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  gap: 3vh;
}

.window-alert{
  box-sizing: border-box;
  padding: 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;

  width: 100vw;
  height: 100vh;

}
.window-alert>h1{
  font-family: 'Mak';
}