#basketWindow{
    box-sizing: border-box;
    padding: 7vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 3vh;
}


#basketWindow.center{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}
#basketWindow .basketNull{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    gap: 2vh;
}
#basketWindow .basketNull p{

    font-family: 'Mak';
    text-align: center;
    font-size: 4vh;


}

#basketWindow .basketNull button{
    align-self: center;

    font-family: 'Nitti';
    font-size: 2.5vh;
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}
#basketWindow .done{
    align-self: center;

    font-family: 'Nitti';
    font-size: 2.5vh;
    color: white;

    min-height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}
.orderList{
    box-sizing: border-box;
    padding-left: 7vw;
    padding-right: 7vw;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 3vh;

    overflow-x: auto;
    min-height: 35vh;
}
.orderList::-webkit-scrollbar{
    display: none;
}

