#feedbackWindow{
    margin-top: 10vh;
    box-sizing: border-box;
    padding: 5vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3vh;

    height: 90vh;
    width: 100%;
}
#feedbackWindow form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    gap: 2vh;
}
#feedbackWindow>h1{
    text-align: center;
    font-family: 'Mak';
    font-size: 3vh;
}
#feedbackWindow form input::placeholder,
#feedbackWindow form textarea::placeholder{
    font-family: 'Nitti';
    font-size: 2.5vh;
}
#feedbackWindow form input,
#feedbackWindow form textarea{
    box-sizing: border-box;
    padding-left: 2vh;

    font-family: 'Nitti';
    font-size: 2.5vh;

    height: 7vh;
    border: .2vh solid black;
}
#feedbackWindow form textarea{
    padding-top: 2vh;
}
#feedbackWindow form textarea{
    height: 30vh;
}
#feedbackWindow form>button{
    align-self: center;

    font-family: 'Nitti';
    font-size: 2.5vh;
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}