#footerMenu{
    box-sizing: border-box;
    padding: 7vw;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 3vh;

    width: 100vw;

    border-radius: 10vw 10vw 0vw 0vw;

    background-color: black;
}
#footerMenu section{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 2vh;
}
#footerMenu section h1{
    font-family: 'Mak';
    color: white;
}
#footerMenu section p,
#footerMenu section a{
    font-family: 'Nitti';
    color: white;
}

