#aboutWindow{
    box-sizing: border-box;
    padding: 7vw;
    padding-top: 0vw;
    
    overflow-y: auto;
    overflow-x: hidden;
}
#aboutWindow>section{
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 5vh;
}
#aboutWindow p{
    font-family: 'Nitti';
    font-size: 2.4vh;
}