#modalWindow{
    box-sizing: border-box;
    padding-left: 7vw;
    padding-right: 7vw;

    position: fixed;
    top: 0vh;
    left: 0vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;

    height: 100%;
    width: 100%;

    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
}
#modalWindow>section{
    box-sizing: border-box;
    padding: 3vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    gap: 3vh;

    border: .2vh solid black; 

    height: 30vh;
    background-color: white;
}
#modalWindow>section>h1{
    font-family: 'Mak';
    font-size: 3vh;
    text-align: center;
    color: black;
}

#modalWindow>section>div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 2vh;
}
#modalWindow>section>div button{
    align-self: center;

    font-family: 'Nitti';
    font-size: 2.5vh;
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}