#rackFeedBack{
    box-sizing: border-box;
    padding: 7vw;
    padding-top: 0vw;
    margin-top: 3vh;
    margin-bottom: 3vh;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 3vh;
}

#rackFeedBack h1{
    font-family: 'Mak';
}
#rackFeedBack p{
    font-family: 'Nitti';
}
#rackFeedBack a{
    font-family: 'Nitti';
    color: black;
}

#rackFeedBack form{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    gap: 2vh;
}
#rackFeedBack form input::placeholder{
    font-family: 'Nitti';
}
#rackFeedBack form input,
#rackFeedBack form textarea{
    box-sizing: border-box;
    padding-left: 2vh;

    font-family: 'Nitti';

    height: 7vh;
    border: .2vh solid black;
}
#rackFeedBack form p{
    font-family: 'Nitti';
}
#rackFeedBack form textarea{
    padding-top: 2vh;
}
#rackFeedBack form textarea{
    height: 30vh;
}
#rackFeedBack form button{
    align-self: center;

    font-family: 'Nitti';
    color: white;

    height: 7vh;
    width: 100%;

    background-color: black;
    border: none;
}
